main {
	position: absolute;
	top: 100px;
	bottom: 0;
	width: 100%;
}

.options-bar {
	position: relative;
	height: 70px;
}

.grid-options {
	position: absolute; /* Also allows SearchList absolute positioning*/
	top: 0; right: 0; bottom: 0; left: 0;
	margin: auto;
	width: 75%;
	display: grid;
	grid-template: 1fr 1fr / 1fr 1fr;
	grid-row-gap: 5px;
}

.content-wrapper {
	position: absolute;
	top: 70px;
	bottom: 0;
	width: 92%;
	margin: 0 4%;
}



