.grid-container-currency {
	display: grid;
	grid: repeat(3, 1fr) / repeat(3, 1fr);
	grid-template-areas:
		"img name name"
		"img rates rates"
		"img rates rates";
	background-color: white;
	color: black;
	border: 1px solid darkgray;
	border-radius: 5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.currency-image {
	grid-area: img;
	place-self: center;
	width: 50%;
	height: auto;
}

.currency-name {
	grid-area: name;
	-ms-flex-item-align: end;
	    align-self: end;
}

.currency-rates {
	grid-area: rates;
	-ms-flex-item-align: center;
	    align-self: center;
}



